.footer {
    display: flex;
    justify-content: space-between;
    padding: 2rem 6rem;
    background: #000;
    color: #fff;
}

.top {
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 40%;
}

.top i {
    color: #fff;
    font-size: 2rem;
    margin-left: 1rem;
}

.top i:hover {
    color: #01959a;
}

.bottom {
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 40%;
}

.bottom div {
    display: flex;
    flex-direction: column;
}

.bottom h4 {
    font-size: 1.3rem;
    padding: 1rem 0 .8rem 0;
}

.bottom a {
    text-decoration: none;
    color: #fafafa;
    padding-bottom: .4rem;
    font-size: 1.1rem;
}

.bottom__content a:hover {
    color: #01959a;
}

@media screen and (max-width: 850px){
    .footer {
        padding: 4rem 2rem;
        display: block;
    }

    .footer i {
        margin: 1rem 1rem 0 0;
    }

    .top,
    .bottom {
        width: 100%;
    }

    .bottom div {
        width: 50%;
    }
}