.trip {
    margin: 6.5rem 6rem 4rem;
    color: #2a2a2a;
}

.trip h1 {
    font-size: 3rem;
}

.trip__text {
    margin-inline: auto;
    font-size: 1.3rem;
    width: 50%;
}

.trip__card {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.t-card {
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, .2);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}

.t-image {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.t-image img {
    width: 100%;
    height: 100%;
    transition: .3s ease-in-out;
    border-radius: 7px;
}

.t-image:hover img {
    transform: scale(1.3);
}

.t-card h4 {
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}

@media screen and (max-width: 850px){
    .trip {
        margin: 4rem 2rem;
    }

    .trip__card {
        flex-direction: column;
    }

    .t-card {
        width: 100%;
        margin-bottom: 1.5rem;
    }

    .trip__text {
        width: 100%;
    }
}